  [class^="btn"],
  [class*=" btn"] {
    position: absolute;
    z-index: 1;
    user-select: none;
    touch-action: none;
    user-select: none;
    -webkit-user-drag: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }

  .btn-left {
    left: 5px;
    top: 50%;
    transform: translateY(-50%);
  }

  .btn-up {
    top: 5px;
    left: 50%;
    transform: translateX(-50%);
  }

  .btn-right {
    right: 5px;
    top: 50%;
    transform: translateY(-50%);
  }

  .btn-down {
    bottom: 5px;
    left: 50%;
    transform: translateX(-50%);
  }

  .spinner {
    $from: 0.5;
    $to: 0;
    $color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    width: 50px;
    height: 40px;
    text-align: center;
    font-size: 14px;

    @keyframes sk-stretchdelay {
      0%,
      40%,
      100% {
        transform: scaleY(0.4);
        opacity: $to;
      }

      20% {
        transform: scaleY(1);
        opacity: $from;
      }
    }

    [class^="rect"] {
      background-color: $color;
      opacity: $from;
      height: 100%;
      width: 6px;
      display: inline-block;
      animation: sk-stretchdelay 1.2s infinite ease-in-out;
    }

    .rect2 {
      animation-delay: -1.1s;
    }

    .rect3 {
      animation-delay: -1s;
    }

    .rect4 {
      animation-delay: -0.9s;
