</style>
<script>
import '@lianj-fex/vue-gesture-frame/dist/index.css';
import GestureFrame from '@lianj-fex/vue-gesture-frame';
import { debounce } from 'throttle-debounce';
import thumb from '@/filters/thumb';
import extract from '_/extract';
import { signUrl, isNeedSignOssUrl } from '@/utils/oss/utils';

function clone(target) {
  return JSON.parse(JSON.stringify(target));
}

const maxDpr = 2;
const dpr = Math.min(global.devicePixelRatio || 1, maxDpr);

function calcSize(size, factor) {
  return size * dpr * factor;
}

function getUrl(url, { width, height, factor }) {
  return thumb(url, {
    width: calcSize(width, factor),
    height: calcSize(height, factor),
  });
}

// !!!布局要求，宿主不能被设置padding和border


export default {
  components: {
    GestureFrame,
  },
  model: GestureFrame.model,
  props: {
    // 参数主要产考GestureFrame
    ...GestureFrame.props,
    items: {},
    // 缩略图的长宽缩放比例
    thumbScaleFactor: {
      default: 0.2,
    },
    maxNumberOfError: {
      default: 3,
    },
    maxScale: {
      default: 3,
    },
    speed: {
      default: () => [9 / 375, -9 / 375],
    },
    cycle: {
      default: () => [false, true],
    },
    // 当放大后，左键作用变为拖动
    leftMouseButtonMovingAfterScale: {
      type: Boolean,
      default: false,
    },
    // 一倍缩放情况是否允许拖动
    canMovingWhen1x: {
      type: Boolean,
      default: true,
    },
    // 拖动时判断拖动偏向，并锁定另一轴
    lockRotateDirection: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    width: null,
    height: null,
    gestureFramsParamsItems: null,
  }),
  computed: {
    gestureFrameParams() {
      const s = {
        ...extract(this, Object.keys(this.$options.props)),
        items: this.gestureFramsParamsItems,
      };
      delete s.thumbScaleFactor;
      return s;
    },
  },
  methods: {
    emit(v) {
      console.log(this.$options.model.event, v);
      this.$emit(this.$options.model.event, v);
    },
    resize: debounce(500, function resize() {
      this.width = this.$el.clientWidth;
      this.height = this.$el.clientHeight;
    }),
  },
  mounted() {
    this.resize();
    global.addEventListener('resize', () => {
      this.resize();
    });
    this.$watch(() => {
      return {
        height: this.height,
        width: this.width,
        items: this.items,
        thumbScaleFactor: this.thumbScaleFactor,
      };
    }, async ({
      width, height, items, thumbScaleFactor,
    }) => {
      let _items = null;
      if (width && height) {
        const size = { width, height };
        _items = await Promise.all(clone(items).map(items2 => {
          return Promise.all(items2.map(async item => {
            const { url } = item;
            // item.url = getUrl(url, { ...size, factor: this.maxScale });
            if (thumbScaleFactor) {
              let _thumb = await getUrl(url, { ...size, factor: thumbScaleFactor });
              if (isNeedSignOssUrl(_thumb)) {
                _thumb = await signUrl(_thumb);
              }
              if (_thumb !== url) {
                item.thumb = _thumb;
              }
            }
            item.url = isNeedSignOssUrl(url) ? await signUrl(url) : url;
            return item;
          }));
        }));
      }

      this.gestureFramsParamsItems = _items;
    });
  },
};
</script>
